.div2 {
  float: left;
  width: 1320px;
  height: 598px;
}
.div2 .titleNode {
  width: 384px;
  height: 120px;
  /* float: left; */
  line-height: 103px;
  text-align: center;
  cursor: pointer;
  position: relative;
  /* margin-left: 88px; */
  font-size: 44px;
  text-shadow: 0 0 30px #f4faf5;
  color: #fff;
}
.div2 .realtime {
  overflow: hidden;
  margin-top: 29px;
  width: 1318px;
  height: 466px;
}
.div2 .realtime .bg {
  margin: 38px 0 0 36px;
  width: 1284px;
  height: 414px;
  background-image: linear-gradient(90deg, rgba(2, 208, 255, 0.1) 0%, rgba(22, 32, 99, 0.1) 100%);
}
.div2 .realtime .bg .weiyang {
  padding: 34px 0 0 58px;
  display: inline-block;
  width: 50%;
  height: 25%;
}
.div2 .realtime .bg .weiyang .zhongduan {
  text-align: center;
  display: inline-block;
  width: 306px;
  height: 96px;
  line-height: 96px;
  font-size: 34px;
  letter-spacing: 2px;
  color: #ffffff;
  overflow: hidden;
}
.div2 .realtime .bg .weiyang .num {
  display: inline-block;
  margin-left: 20px;
  font-weight: 700;
  font-size: 49px;
  color: #17e6ff;
}
.div2 .realtime .bg .weiyang .num i {
  margin-left: 10px;
  font-size: 31px;
  font-weight: normal;
  color: #fff;
  font-style: normal;
}
